import {
	getJob,
} from '@/api/job'
import citys from '@/utils/data'
import publish from '@/utils/items/publish'
import industrys from '@/utils/items/industry'
import schooling from '@/utils/items/schooling'
import working from '@/utils/items/working'
import nature from '@/utils/items/nature'
import size from '@/utils/items/size'
import {
	$wuxToast,
} from '../../wxcomponents/wux/index'
import {
	mapGetters
} from 'vuex'
export default {
	computed: {
		...mapGetters([
			'address',
		])
	},
	data() {
		return {
			ready: false,
			filter: {
				active: null,
				industry: {
					items: industrys,
					show: false,
					active: 0,
					id: null,
					text: '全部行业'
				},
				salary: {
					show: false,
					def: [0],
					value: null
				},
				publish: {
					items: publish,
					show: false,
					text: null,
					id: null,
				},
				more: {
					show: false,
					schooling: {
						items: schooling,
						text: null,
						id: null,
					},
					working: {
						items: working,
						text: null,
						id: null,
					},
					nature: {
						items: nature,
						text: null,
						id: null,
					},
					size: {
						items: size,
						text: null,
						id: null,
					},
					type: {
						items: [{
								text: '全职',
								id: 1,
							},
							{
								text: '兼职',
								id: 2,
							},
							{
								text: '实习',
								id: 3
							}
						],
						text: null,
						id: null,
					},
				}
			},
			locations: {
				province: null,
				city: null,
				area: null,
				citys: '全国',
			},
			list: null,
			count: null,
			search: {
				industry: null,
				salary: null,
				publish: 0,
				schooling: null,
				working: null,
				nature: null,
				size: null,
				type: null,
				value: null,
				count: true,
				page: 1,
				limit: 10
			},
			city: {
				show: false,
				options: citys
			},
			showlist: false
		}
	},
	onShow() {
		this.$store.commit('SET_LTABBAR', 'job');
		this.showlist = true
		this.getJob();
	},
	onReady() {
		this.ready = true
	},
	onLoad(e) {
		uni.hideTabBar()
		if (e.search) {
			this.search.value = e.search
		}
	},
	onPullDownRefresh() {
		this.refresher()
		uni.stopPullDownRefresh();
	},
	onReachBottom() {
		this.refresher()
	},
	methods: {
		getJob() {
			const s = this.search
			const t = this
			const a = this.address
			let sl = this.showlist
			const search = Object.assign({}, {
					count: s.count,
					page: sl ? 1 : s.page,
					limit: sl ? 10 * s.page : s.limit,
				},
				s.industry ? {
					industry: s.industry
				} : {},
				s.salary ? {
					salary: s.salary
				} : {},
				s.publish && s.publish > 0 ? {
					publish: s.publish
				} : {},
				s.schooling && s.schooling > 0 ? {
					schooling: s.schooling
				} : {},
				s.working && s.working > 0 ? {
					working: s.working
				} : {},
				s.nature && s.nature > 0 ? {
					nature: s.nature
				} : {},
				s.size && s.size > 0 ? {
					size: s.size
				} : {},
				s.type ? {
					type: s.type
				} : {},
				s.value ? {
					value: s.value
				} : {},
				a.province ? {
					province: a.province
				} : {},
				a.city ? {
					city: a.city
				} : {},
				a.area ? {
					area: a.area
				} : {},
			);
			getJob(search).then(response => {
				let r = response.data
				if (response.code === 20000) {
					t.count = r.list.count
					if (t.list && t.list.length > 0 && !sl) {
						t.list = t.list.concat(r.list.rows)
					} else {
						sl = false
						t.list = r.list.rows
					}
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
				}
			})
		},
		refresher() {
			let t = this
			if (t.count > t.list.length) {
				t.search.page = t.search.page++
				this.getJob()
			} else {
				$wuxToast().hide()
				$wuxToast().show({
					type: 'forbidden',
					text: '已加载全部'
				})
			}
		},
		editCity(e) {
			let s = this.locations
			let address = e.detail.options.map((n) => n.label)
			let arr = e.detail.options.map((n) => n.label)
			if (arr.length === 1) {
				s.citys = address[0]
			} else if (arr.length === 2) {
				s.citys = address[1]
			} else if (arr.length === 3) {
				s.citys = address[2]
			}
			s.province = arr[0]
			s.city = arr[1]
			s.area = arr[2]
			this.$store.commit('SET_ADDRESS', s)
			this.list = null
			this.getJob()
		},
		salary(e) {
			this.filter.salary.value = e.detail.value[0]
		},
		onClickNav(e) {
			this.filter.industry.active = e.detail.index
		},
		onClickItem(e) {
			this.search.industry = e.detail.id
			const f = this.filter
			f.industry.show = false
			f.active = null
			f.industry.id = e.detail.id
			f.industry.text = e.detail.text
			this.list = null
			this.getJob()
		},
		onChange(e) {
			const d = e.detail
			const f = this.filter
			f.active = d
			f.industry.show = d === 0 ? true : false
			f.salary.show = d === 1 ? true : false
			f.publish.show = d === 2 ? true : false
			f.more.show = d === 3 ? true : false
		},
		filterClose(n) {
			let f
			if (n === 'industry') {
				f = this.filter.industry
			} else if (n === 'salary') {
				f = this.filter.salary
			} else if (n === 'publish') {
				f = this.filter.publish
			} else if (n === 'more') {
				f = this.filter.more
			}
			f.show = false
			this.filter.active = null
		},
		filterClick(t, i, n) {
			let p
			if (n === 'publish') {
				p = this.filter.publish
			} else if (n === 'schooling') {
				p = this.filter.more.schooling
			} else if (n === 'working') {
				p = this.filter.more.working
			} else if (n === 'nature') {
				p = this.filter.more.nature
			} else if (n === 'size') {
				p = this.filter.more.size
			} else if (n === 'type') {
				p = this.filter.more.type
			}
			p.text = t
			p.id = i
		},
		upPublish() {
			const f = this.filter
			this.search.publish = f.publish.id ? f.publish.id : 0
			f.active = null
			f.publish.show = false
			this.list = null
			this.getJob()
		},
		upMore() {
			const f = this.filter
			const s = this.search
			s.schooling = f.more.schooling.id
			s.working = f.more.working.id
			s.nature = f.more.nature.id
			s.size = f.more.size.id
			s.type = f.more.type.id
			f.active = null
			f.more.show = false
			this.list = null
			this.getJob()
		},
		upSalary() {
			const f = this.filter
			this.search.salary = f.salary.value * 1000
			f.active = null
			f.salary.show = false
			this.list = null
			this.getJob()
		},
		detailTo(id) {
			uni.navigateTo({
				url: '/pages/job/detail?id=' + id
			});
		},
		getSearch(e) {
			this.search.value = e.detail
			this.list = null
			this.getJob()
		},
	}
}
